import React from "react";
import logo from "../images/logo (1).svg"
import iconTelegram from "../images/Vector.svg"
import iconVk from "../images/Vector (1).svg"
import iconPhone from "../images/.svg"


function Footer() {
  return (
    <footer className="footer">
      <div className="footer__one-column">
        <img className="footer__logo" src={logo} alt="" />
        <p className="footer__copyrigth"> YouMeal, 2023.  </p>
        <p className="footer__disaner">Design: Anastasia Ilina</p>
      </div>
      <div className="footer__two-column">
        <div >
          <p className="footer__text">  </p>
          <div className="footer__wrapper-contact">
            <img className="footer__image-tel" src={iconPhone} alt=" " />
            <p className="footer__num">+7(930)833-38-11</p>
          </div>
        </div>
        <div >
          <p className="footer__about-text">  </p>
          <nav className="footer__social">
            <a className="footer__link"><img className="footer__icon" src={iconTelegram} alt=" " /></a>
            <a className="footer__link"><img className="footer__icon" src={iconVk} alt=" " /></a>
          </nav>
        </div>
      </div >
    </footer>
  )
}

export default Footer