import React from 'react';
import ponchik from "../images/.png"


function PopupWithForm({ isOpen, handleClose }) {

  const [radioDostavka, setRadioDostavka] = React.useState(true);
  const [radioSamovizov, setRadioSamovizov] = React.useState(false);

  function handleRadio() {
    if (radioDostavka) {
      setRadioDostavka(false);
      setRadioSamovizov(true);
    } else {
      setRadioDostavka(true);
      setRadioSamovizov(false);
    }
  }

  function handleOverlayClose(evt) {
    if (evt.target === evt.currentTarget) {
      //  
      handleClose();
    }
  }

  React.useEffect(() => {
    //  esc
    function handleEscClose(evt) {
      if (evt.key === "Escape") {
        //  
        handleClose();
      }
    }

    if (isOpen) {
      document.addEventListener("keydown", handleEscClose);
    }

    return () => {
      document.removeEventListener("keydown", handleEscClose);
    }
  }, [isOpen, handleClose]);

  function handleSubmit(e) {
    e.preventDefault();
    handleClose();
  }

  return (
    <div onMouseDown={handleOverlayClose} className={`popup ${isOpen && "popup_opened"}`} onSubmit={handleSubmit}>
      <div className='popup__container'>
        <button className='popup__close-btn' type='button' onClick={handleClose} />
        <div className='popup__container-image'>
          <img src={ponchik} alt='' className='popup__image' />
        </div>
        <form className='popup__form'>
          <h2 className='popup__title'></h2>
          <input className='popup__input' placeholder=' ' type='text' />
          <input className='popup__input' placeholder='' type='tel' />
          <fieldset className='popup__fieldset-chek'>
            <label className='popup__label' >
              <input type='radio' checked={radioSamovizov} onClick={handleRadio} />
              <span></span>
            </label>
            <label className='popup__label'>
              <input type='radio' name='format' checked={radioDostavka} onClick={handleRadio} />
              <span></span>
            </label>
          </fieldset>
          {radioDostavka &&
            <>
              <input className='popup__input' placeholder=', , ' type='text' />
              <fieldset className='popup__fieldset-input-dostavka'>
                <input className='popup__input' placeholder='' type='number' />
                <input className='popup__input' placeholder='' type='number' />
              </fieldset>
            </>
          }
          <button className={`popup__button popup__save-btn`} type='submit' ></button>
        </form>

      </div>
    </div>
  )
}

export default PopupWithForm;